import React, { useState, useEffect } from 'react';

const Header = ({ onServicesClick, onAboutUsClick, onContactClick }) => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <header className={`fixed w-full z-50 transition-all duration-300 ${isScrolled ? 'bg-blue-900/95 backdrop-blur-sm shadow-lg' : 'bg-transparent'}`}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-14">
          {/* Logo */}
          <div className="flex-shrink-0 flex items-center">
            <span className="text-2xl font-bold bg-gradient-to-r from-white to-blue-300 bg-clip-text text-transparent">
              Sermain
            </span>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:block">
            <ul className="flex space-x-8">
              <li>
                <button onClick={onServicesClick} className="text-white hover:text-blue-300 transition-colors duration-300 text-lg font-medium relative group">
                  Servicios
                  <span className="absolute bottom-0 left-0 w-full h-0.5 bg-blue-300 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300" />
                </button>
              </li>
              <li>
                <button onClick={onAboutUsClick} className="text-white hover:text-blue-300 transition-colors duration-300 text-lg font-medium relative group">
                  Nosotros
                  <span className="absolute bottom-0 left-0 w-full h-0.5 bg-blue-300 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300" />
                </button>
              </li>
              <li>
                <button onClick={onContactClick} className="text-white hover:text-blue-300 transition-colors duration-300 text-lg font-medium relative group">
                  Contacto
                  <span className="absolute bottom-0 left-0 w-full h-0.5 bg-blue-300 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300" />
                </button>
              </li>
            </ul>
          </nav>

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)} className="text-white p-2">
              <svg className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                {isMobileMenuOpen ? (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                ) : (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                )}
              </svg>
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        <div className={`md:hidden transition-all duration-300 ease-in-out ${isMobileMenuOpen ? 'max-h-48 opacity-100' : 'max-h-0 opacity-0'} overflow-hidden`}>
          <ul className="pb-4 space-y-2">
            <li>
              <button onClick={() => { onServicesClick(); setIsMobileMenuOpen(false); }} className="block text-white hover:text-blue-300 py-2 text-lg">
                Servicios
              </button>
            </li>
            <li>
              <button onClick={() => { onAboutUsClick(); setIsMobileMenuOpen(false); }} className="block text-white hover:text-blue-300 py-2 text-lg">
                Nosotros
              </button>
            </li>
            <li>
              <button onClick={() => { onContactClick(); setIsMobileMenuOpen(false); }} className="block text-white hover:text-blue-300 py-2 text-lg">
                Contacto
              </button>
            </li>
          </ul>
        </div>
      </div>
    </header>
  );
};

export default Header;
